import axios from '../../repositories/Repository'
export default {
    
    state: {
        status: '',
        token: localStorage.getItem('token') || '',
        user: {}        
    },
    getters: {
        isLoggedIn: state => !!state.token,
        authStatus: state => state.status,
        get_user: state => state.user,
    },
    mutations: {
        auth_success(state, token){
		    state.status = 'success'
		    state.token = token
	  	},        
	  	logout(state){
	    	state.status = ''
	    	state.token = ''
        },
        set_user: (state, user) => {
            state.user = user;
        }
    },
    actions: {
        REGISTER: ({commit}, payload) => {
            return new Promise( (resolve, reject) => {
                axios
                .post(`/api/users/nuevo-usuario`, payload, { headers: {'Content-Type': 'application/json'} })
                .then(({data, status}) => {
                    if (status === 200 ) {
	                    // const token = data.token
	                    // const user = data.user
                        // localStorage.setItem('token', token)
                        // commit("auth_success", token)                        
                        // commit("set_user", user)                        
                        resolve(true);
                    }
                })
                .catch(error => {
                    console.log(error);
                    reject(error);
                })
            })
        },
        LOGIN: ({commit}, payload) => {
            return new Promise( (resolve, reject) => {
                axios
                .post(`/api/login`, payload, { headers: {'Content-Type': 'application/json'} })
                .then(({data, status}) => {
                    if (status === 200 ) {
                        console.log(data)
                        const token = data.token
                        const user = data.usuarioDB
                        console.log(user)
                        localStorage.setItem('token', token)
                        commit("auth_success", token)                        
                        commit("set_user", user)                        
                        resolve(true);
                    }
                })
                .catch(error => {
                    console.log(error);
                    reject(error);
                })
            })
        },        
        logout({commit}){
            return new Promise((resolve, reject) => {
              commit('logout')
              localStorage.removeItem('token')
              //delete axios.defaults.headers.common['Authorization']
              resolve()
            })
          }        
    }
}