<template>
  <div>
    <component :is="layout">
      <router-view/>
    </component>
  </div>
</template>

<script>
const default_layout = "app"
export default {
  data: () => ({

  }),
  computed: {
    layout() {
      return (this.$route.meta.layout || default_layout) + '-layout'
    } 
 
  }
}
</script>
